import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import Moods from './views/Moods.vue'
import Windows from './views/Windows.vue'
import Words from './views/Words.vue'
import Privacy from './views/Privacy.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/moods',
      name: 'moods',
      component: Moods
    },
    {
      path: '/moods/:id',
      name: 'moviemoodsdetail',
      component: Moods
    },
    {
      path: '/windows',
      name: 'windows',
      component: Windows
    },
    {
      path: '/words',
      name: 'words',
      component: Words
    },
    {
      path: '/privacy',
      name: 'privacy',
      component: Privacy
    },
    {
      path: '/movies',
      name: 'movies',
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () => import(/* webpackChunkName: "about" */ './views/Movies.vue')
    }
  ]
})
